<template>
  <div class="batch-import">
    <!--  -->
    <el-button
      type="success"
      icon="el-icon-upload"
      @click="dialogVisible=true"
    >{{btnText}}</el-button>
    <!--  end -->

    <!--   -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      modal-append-to-body
      lock-scroll
      width="92%"
    >
      <el-upload
        class="upload"
        action=""
        :multiple="false"
        :show-file-list="false"
        accept="csv, application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
        :http-request="httpRequest"
      >
        <el-button
          type="primary"
          icon="el-icon-upload"
        ></el-button>
        <div
          class="el-upload__tip"
          slot="tip"
        >
          excel<span v-if="size">{{size}}kb</span>
        </div>
      </el-upload>
      <el-button-group>
        <el-button class="download" @click="downLoadExcel"></el-button>
        <el-button class="edit" v-if="tableData.length" @click="handleToggleEdit">{{!isEdit ?'' : ''}}</el-button>
      </el-button-group>

      <y-table
        :max-height="360"
        :data="tableData"
        :columns="currentColumns"
        pagination
        :total="total"
        :reload="reloadData"
        :colIndex="1"
      ></y-table>

      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleCancel"> </el-button>
        <el-button
          type="primary"
          @click="handleConfirm"
        > </el-button>
      </span>
    </el-dialog>
    <!--  end -->
  </div>
</template>

<script>
import XLSX from 'xlsx'
import { merge, find, cloneDeep } from 'lodash'
// import Vue from 'vue'
export default {
  name: 'YBatchImport',
  data() {
    return {
      dialogVisible: false,
      tableData: [],
      total: 0,
      queryParams: {
        current: 1,
        size: 10
      },
      dbData: [],
      currentColumns: cloneDeep(this.columns),
      isEdit: false
    }
  },
  props: {
    btnText: {
      type: String,
      default: ''
    },
    size: {
      type: [String, Number],
      default: 0
    },
    downLoadUrl: {
      type: String
      // required: true
    },
    columns: {
      type: Array,
      default() {
        return [
          {
            label: '',
            prop: 'name',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].name} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.name}</div>
              }
            }
          },
          {
            label: '',
            prop: 'brand',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].brand} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.brand}</div>
              }
            }
          },
          {
            label: '',
            prop: 'spec',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].spec} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.spec}</div>
              }
            }
          },
          {
            label: '',
            prop: 'l1Name',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].l1Name} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.l1Name}</div>
              }
            }
          },
          {
            label: '',
            prop: 'l2Name',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].l2Name} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.l2Name}</div>
              }
            }
          },
          {
            label: '',
            prop: 'l3Name',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].l3Name} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.l3Name}</div>
              }
            }
          },
          {
            label: '',
            prop: 'manufacturer',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].manufacturer} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.manufacturer}</div>
              }
            }
          },
          {
            label: '',
            prop: 'shelfLifeDays',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].shelfLifeDays} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.shelfLifeDays}</div>
              }
            }
          },
          {
            label: '',
            prop: 'priceUnit',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].priceUnit} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.priceUnit}</div>
              }
            }
          },
          {
            label: ',',
            prop: 'normal',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].normal} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.normal}</div>
              }
            }
          },
          {
            label: '',
            prop: 'saleTaxRate',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].saleTaxRate} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.saleTaxRate}</div>
              }
            }
          },
          {
            label: '',
            prop: 'saleType',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].saleType} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.saleType}</div>
              }
            }
          },
          {
            label: '',
            prop: 'taxRate',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].taxRate} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.taxRate}</div>
              }
            }
          },
          {
            label: '',
            prop: 'temperature',
            render: (h, { row }) => {
              if (this.isEdit) {
                return <el-input v-model={this.tableData[row.index].temperature} size='small' onBlur={this.handleToggleEdit}></el-input>
              } else {
                return <div onClick={this.handleToggleEdit}>{row.temperature}</div>
              }
            }
          }
        ]
      }
    }
  },
  methods: {
    // 
    handleCancel() {
      this.tableData = []
      document.getElementsByName('file')[0].value = ''
      this.dialogVisible = false
    },
    // 
    handleConfirm() {
      this.mergeTable()
      this.$emit('upload', this.dbData)
    },
    // 
    handleToggleEdit() {
      this.isEdit = !this.isEdit
      // tableDatadbData
      if (!this.isEdit) {
        this.mergeTable()
      }
    },
    // tableDatadbData
    mergeTable() {
      const { size, current } = this.queryParams
      console.log('this.tableData', this.tableData)
      const mergeData = this.tableData.map(item => {
        return {
          ...item,
          index: undefined
        }
      })
      this.dbData.splice((current - 1) * size, size, ...mergeData)
    },
    // 
    downLoadExcel() {
      if (!this.downLoadUrl) {
        return this.$message.warning('')
      }
      const url = encodeURI(this.downLoadUrl)
      window.location.href = url
    },
    // 
    loadData() {
      const { size, current } = this.queryParams
      this.tableData = this.dbData.slice((current - 1) * size, current * size).map((item, index) => {
        item.index = index
        return item
      })
    },
    // 
    reloadData({ type, pageSize: size, currentPage }) {
      if (type === 'size-change') {
        // current1
        this.queryParams = merge(this.queryParams, { size, current: 1 })
      } else {
        // 
        this.queryParams = merge(this.queryParams, { current: currentPage })
      }
      this.loadData()
      this.mergeTable()
    },
    // dbData excel
    formatDbData(dbData) {
      if (!Array.isArray(dbData)) {
        throw new TypeError('Array')
      }
      const res = dbData.map(item => {
        const obj = {}
        Object.keys(item).forEach(key => {
          // keycolumn
          const column = find(this.columns, col => {
            return col.label === key
          })
          if (column) {
            obj[column.prop] = item[key]
          }
        })
        return obj
      })
      return res
    },
    // 
    httpRequest(e) {
      this.tableData = []
      const file = e.file // 
      if (!file) {
        // 
        return false
      } else if (!/\.(xls|xlsx)$/.test(file.name.toLowerCase())) {
        // 
        this.$message.error('xlsxlsx')
        return false
      }

      const fileReader = new FileReader()
      fileReader.onload = (ev) => {
        try {
          const data = ev.target.result
          const workbook = XLSX.read(data, {
            type: 'binary' // 
          })
          const exlname = workbook.SheetNames[0] // 
          const exl = XLSX.utils.sheet_to_json(workbook.Sheets[exlname]) // json
          //  JSON  data 
          this.dbData = this.formatDbData(exl)
          this.loadData()
          this.total = this.dbData.length
          // document.getElementsByName('file')[0].value = '' // value
        } catch (e) {
          console.log('')
          return false
        }
      }
      fileReader.readAsBinaryString(file)
    }
  }
}
</script>

<style scoped>
.el-upload__tip {
  margin-top: 20px;
  color: #999;
}
.download {
  margin-top: 20px;
}
.edit {
  margin-top: 20px;
  margin-left: 20px;
}
</style>
